import React,{useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';


import firebase from '../../firebase'
import {useDispatch, useSelector} from 'react-redux';
import {addOffers, offersSelector} from '../../features/offers'


function getModalStyle() {
  const top = 50 ;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  input:{
    display:'none'
  },
  paper: {
    position: 'absolute',
    width: 800,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function NewOffer() {
  const classes = useStyles();
  // getModalStyle is not a pure function, we roll the style only on the first render
  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);

  const [offerCode, setOfferCode] = useState('');
  const [discount, setDiscount] = useState('');

  const dispatch = useDispatch();


  const handleOpen = () => {
    setOpen(true);

    setOfferCode('');
    setDiscount('');

  };

  const handleSave = (e) => {

    handleClose();
    const data = {
      offerCode,
      discount,
      created: firebase.firestore.FieldValue.serverTimestamp(),
      modified: firebase.firestore.FieldValue.serverTimestamp()
    }

    firebase.firestore().collection('offers').doc().set(data)
      .then(dispatch(addOffers(offerCode)));
    
  }

  const handleClose = () => {
    setOpen(false);
  };


  const body = (
    <div style={modalStyle} className={classes.paper}>
      <form>
        <Grid
          container
          spacing={2}
        >
          <Grid
            container
            item
            spacing={2}
          >
            <Grid 
              item
              xs={8}            
              >
                <TextField id="filled-basic" label="Offer Code" variant="outlined" fullWidth value={offerCode} onChange={e=>setOfferCode(e.target.value)}/>
            </Grid>
            <Grid 
              item
              xs={4}
              >
                <TextField id="filled-basic" label="Discount %" variant="outlined" value={discount} onChange={e=>setDiscount(e.target.value)}/>
            </Grid>
          </Grid>

            <Grid
              item
              container
              spacing={2}
            >
     
              <Grid
                container
                item
                spacing={2}
              >
                <Grid
                  item
                  xs={4}
                >
                </Grid>
                <Grid
                  item
                  xs={2}
                >
                  <Button
                    variant="contained"
                    color="secondary"
                    onClick={handleClose}
                  >Cancel</Button>
                </Grid>
                <Grid
                  item
                  xs={2}
                >
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={handleSave}
                  >Save</Button>
                </Grid>
                <Grid
                  item
                  xs={4}
                >
                </Grid>

              </Grid>

            </Grid>
        </Grid>
      </form>
    </div>
  );

  return (
    <div>
      <Button variant="contained" color="primary" onClick={handleOpen}
        style={{ marginBottom:20 }}
      >
        New
      </Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>
    </div>
  );
}
